@charset "utf-8";
kayamba {margin:0;padding:0;display:block;}
* {box-sizing:border-box;}
body {margin:0;}
img {border:0;}
.color-title {color:#ffb416;}
a {text-decoration:none;color:white;font-weight:bolder;display:inline-block;padding:15px;}
header {background-color:#242428;min-height:100vh;padding:0;margin:0;}

nav {margin:auto; width:100%;background-color:#36486b}
nav > section{display:inline-block;}
nav > section:first-child {width:30%;}
nav > section:first-child h1 {text-align:center; color:white;}
nav > section:last-child {width:70%;text-align:right;}

header #header-body {color:white; text-align:center;}
header #header-body h1 {font-size:6vw;}
header #header-body p {font-size:3vw;}

main {margin:0;}
main section:first-child {text-align:center; min-height:100vh; color:#242428;}
main section:first-child h1 {font-size:4vw;}
main section:first-child h1 ~ p {font-size:2vw; margin:0 20%;}
main section:first-child section {display:flex;margin:auto;flex-wrap:wrap;}
main section:first-child section div {width:45%; margin:auto;}
main section:first-child section div:first-child p {font-size:5vw; text-align:right;padding-left:40px;}
main section:first-child section div:last-child p {font-size:10vw; text-align:left;margin-top:20%;}

@media screen and (max-width:500px){
    header {height:60px;}

    nav {height:100px;overflow:scroll;}
    nav > section{display:block;}
    nav > section a {display:block;text-align:center;}
    nav > section:first-child {background-color:#242428;}
    nav > section:first-child h1 {margin:13px auto;}
    nav > section:first-child, nav > section:last-child {width:100%;height:50px;border:1px solid transparent;}
    nav > section:first-child {position:fixed;}
    nav > section:last-child {margin-top:50px;}

    header #header-body h1 {font-size:12vw;margin-top:20vh;}
    header #header-body p {font-size:6vw;margin-top:10vh;}
    

}